"use client";
import { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import axios from "axios";

export default function HeroSection() {
  const [message, setMessage] = useState("");

  useEffect(() => {
    axios.get("http://localhost:8000/api/hello/").then((res) => {
      setMessage(res.data.message);
    });
  }, []);

  return (
    <section className="w-full bg-white py-16 shadow-md rounded-2xl mb-8">
      <div className="max-w-7xl mx-auto px-4 text-center">
        <h1 className="text-5xl font-bold text-blue-600 mb-4">{message || "Welcome!"}</h1>
        <p className="text-base font-medium text-gray-600 mb-8">
          Empowering innovation with technology solutions for startups and businesses.
        </p>
        <Button className="bg-purple-600 text-white px-8 py-4 rounded-2xl text-lg font-bold shadow-md hover:bg-purple-700 transition">
          Get Started
        </Button>
      </div>
    </section>
  );
} 